/**
 * 
 * @param {Function} callback - 
 * @param {number} [duration=1000] - 
 * @param {'forward'|'backward'} [direction='forward'] - forward: 01backward: 10
 */
export function animateValue(callback, duration = 1000, direction = "forward") {
  let start;
  let rafId;

  const animate = (timestamp) => {
    if (!start) start = timestamp;
    const elapsed = timestamp - start;

    // 
    let currentValue;
    if (direction === "forward") {
      currentValue = elapsed / duration;
    } else {
      currentValue = 1 - elapsed / duration;
    }

    //  0-1 
    currentValue = Math.max(0, Math.min(1, currentValue));

    callback(currentValue);

    if (elapsed < duration) {
      rafId = requestAnimationFrame(animate);
    } else {
      // 
      callback(direction === "forward" ? 1 : 0);
    }
  };

  rafId = requestAnimationFrame(animate);
  return () => cancelAnimationFrame(rafId);
}
