const imageContext = require.context(
  "../assets/images", // 
  true, // 
  /\.(png|jpe?g|svg|webp)$/i // 
);

const images = imageContext.keys().reduce((acc, path) => {
  const fileName = path
    .split("/")
    .pop()
    .replace(/\.[^/.]+$/, "") // 
    .toLowerCase();
  acc[fileName] = imageContext(path); // 
  return acc;
}, {});

export default images;
