import React, {
  useState,
  Children,
  useEffect,
  forwardRef,
  useImperativeHandle,
} from "react";
import { animateValue } from "../utils/animateValue";
import images from "../utils/imageLoader";
import classNames from "classnames";
import "./SwiperContainer.css";
import { Dropdown } from "antd";
import { tags, navIndex, notShowNav } from "../const/const";
import { debounce } from "../utils/utils";
import {Howl} from 'howler';
import assetsBgm from '../assets/audios/audio3.mp3';

const Page = ({ children, visible, index, goToPage, handleMusicClick, isPaused }) => {
  const [disPlay, setDisPlay] = useState(false);
  const [opcity, setOpacity] = useState(0);
  const [isOpen, setIsOpen] = useState(false);

  useEffect(() => {
    setTimeout(() => {
      setDisPlay(visible);
    }, 600);
    if (visible) {
      animateValue((value) => {
        setOpacity(value);
      }, 500);
    } else {
      animateValue(
        (value) => {
          setOpacity(value);
        },
        500,
        "backward"
      );
    }
  }, [visible]);

  const handleBackClick = () => {
    goToPage(1);
  };

  const _handleOpenChange = debounce((open) => {
    setIsOpen(open);
  }, 100);

  const handleJumpEvent = (className) => {
    const jumpPage = document.getElementsByClassName(className);
    const index = parseInt(jumpPage[0].parentElement.dataset.index); // 
    goToPage(index); // 
  };

  return (
    <div
      style={{ display: disPlay ? "block" : "", opacity: opcity }}
      data-index={index}
      className={classNames("page", {
        "page-visible": visible,
        "page-hidden": !visible,
      })}
    >
      <img
        onClick={handleBackClick}
        className={classNames("back", {
          ["back-show"]: ![0, 1].includes(index),
        })}
        src={images["back"]}
        alt=""
      />
      <img
        onClick={handleMusicClick}
        className={classNames("music", {
          ["back-show"]: ![0, 1].includes(index),
        })}
        style={{ display: isPaused ? "none" : "block" }}
        src={images["music"]}
        alt=""
      />
      <img
        onClick={handleMusicClick}
        className={classNames("music-stop", {
          ["back-show"]: ![0, 1].includes(index),
        })}
        style={{ display: isPaused ? "block" : "none" }}
        src={images["music-stop"]}
        alt=""
      />
      <div
        className="nav"
        style={{ display: notShowNav.includes(index) ? "none" : "flex" }}
      >
        {tags.map((tag) => {
          if (tag.items) {
            return (
              <Dropdown
                onOpenChange={_handleOpenChange}
                key={tag.key}
                menu={{
                  items: tag.items,
                  onClick: ({ key }) => handleJumpEvent(key),
                }}
              >
                <div
                  className={classNames("nav-wrap", {
                    nav_active: navIndex[tag.key].includes(index),
                  })}
                >
                  <div className="nav_item">{tag.label}</div>
                </div>
              </Dropdown>
            );
          } else {
            return (
              <div
                onClick={() => handleJumpEvent(tag.page)}
                className={classNames("nav-wrap", {
                  nav_active: navIndex[tag.key].includes(index),
                })}
              >
                <div className="nav_item">{tag.label}</div>
              </div>
            );
          }
        })}
      </div>
      <div
        className="shallow fadeInMask"
        style={{ display: isOpen ? "block" : "none" }}
      ></div>
      <img className="arrow-img" src={images["arrow-up"]} alt="" />
      {children}
    </div>
  );
};

const SwiperContainer = forwardRef(({ children }, ref) => {
  const pages = Children.toArray(children);
  const [currentPage, setCurrentPage] = useState(0);
  const [touchStart, setTouchStart] = useState(0);
  const [isPaused, setIsPaused] = useState(false);

  // Web Audio 
  const soundBgm = new Howl({
    src: [assetsBgm],
    loop: true,
    preload: true,
  })
  
  //  load 
  soundBgm.on('load',()=>{
    window.WeixinJSBridge && window.WeixinJSBridge.invoke('getNetworkType', {},  ()=> {
      soundBgm.play();
    }, false);
  })

  useEffect(() => {
    let startY = 0;
    document.addEventListener(
      "touchstart",
      (e) => {
        startY = e.touches.clientY;
      },
      { passive: false }
    );

    document.addEventListener(
      "touchmove",
      (e) => {
        const deltaY = e.touches.clientY - startY;
        // 
        if (deltaY > 50) {
          // 50px
          e.preventDefault();
        }
      },
      { passive: false }
    );
  }, []);
  

  const handleMusicClick = () => {
    const currentPaused = !isPaused;
    setIsPaused(currentPaused);
    currentPaused ? soundBgm.play() : soundBgm.pause();
  };

  const handleTouchStart = (e) => {
    // y
    setTouchStart(e.touches[0].clientY);
  };

  const handleTouchEnd = (e) => {
    // y
    const touchEnd = e.changedTouches[0].clientY;
    // y
    const delta = touchEnd - touchStart;

    // y50
    if (Math.abs(delta) < 50) return;

    // y0
    if (delta > 0 && currentPage > 0) {
      // 
      goToPage(currentPage - 1);
    }
    // y1
    else if (delta < 0 && currentPage < pages.length - 1) {
      // 
      goToPage(currentPage + 1);
    }
  };

  const goToPage = (newPage) => {
    setCurrentPage(newPage);
  };

  useImperativeHandle(ref, () => ({
    goToPage,
  }));

  return (
    <div
      className="container"
      onTouchStart={handleTouchStart}
      onTouchEnd={handleTouchEnd}
    >
      {pages.map((page, index) => (
        <Page
          key={index}
          visible={index === currentPage}
          index={index}
          goToPage={goToPage}
          handleMusicClick={handleMusicClick}
          isPaused={isPaused}
        >
          {page}
        </Page>
      ))}
    </div>
  );
});

export default SwiperContainer;
