export function debounce(fn, delay = 300, immediate = false) {
  let timer = null;
  let isInvoked = false;

  const debounced = function (...args) {
    const context = this;

    // 
    if (timer) clearTimeout(timer);

    // 
    if (immediate && !isInvoked) {
      fn.apply(context, args);
      isInvoked = true;
    } else {
      // 
      timer = setTimeout(() => {
        if (!immediate) {
          fn.apply(context, args);
        }
        isInvoked = false;
      }, delay);
    }
  };

  // 
  debounced.cancel = () => {
    clearTimeout(timer);
    timer = null;
    isInvoked = false;
  };

  return debounced;
}
